.main {
  width: 100%;
  z-index: 1;
  display: flex;
  margin: 0;
  padding: 0;
  padding-top: 163px;
  .multicolumns {
      width: 100%;
      &__field {
          // position: relative;
          display: flex;
          height: 60px;
          width: 100%;
          border-top: 1px solid grey;
          border-left: 1px solid black;
      }
      &__line {
          display: flex;
      }
  }
  .columnHour {
      display: flex;
      flex-direction: column;
      color: #70757a;
      min-height: 60px;
      width: 55px;
      padding: 0;
      margin: 0;
      flex-flow: row-reverse;
  }
  .columnHour:first-child {
      margin-top: 50px;
  }
  .myEvent {
      width: 200px;
      height: 200px;
      background-color: red;
  }
}

.redline {
  position: absolute;
  width: 100px;
  height: 2px;
  background-color: red;
  top: 0px;
  left: 0;
  z-index: 20;
  &__ball {
      position: relative;
      bottom: 6px;
      right: 6px;
      height: 12px;
      width: 12px;
      border-radius: 100%;
      background-color: red
  }
}